        cursor: pointer;
        -webkit-transition: opacity 800ms, z-index 300ms, -webkit-transform 1s;
        transition: opacity 800ms, z-index 300ms, -webkit-transform 1s;
        transition: opacity 800ms, z-index 300ms, transform 1s;
