
.logo {
  display: inline-block;
  margin: 8px;
  position: relative;
  width: 4em;
  height: 4em;
}
.logo:before {
    position: absolute;
    margin: -2em;
    left: 50%;
    top: 50%;
    width: 4em;
    height: 4em;
    -webkit-border-radius: 50%;
            border-radius: 50%;
    content: '';
    z-index: 1;
    background: conic-gradient(#ff8080, #ffbf80, #ffff80, #bfff80, #80ff80, #80ffbf, #80ffff, #80bfff, #8080ff, #bf80ff, #ff80ff, #ff80bf, #ff8080);
    -webkit-animation: whirl 2s linear infinite;
            animation: whirl 2s linear infinite;
    -webkit-box-shadow: 0 0 10px currentColor;
            box-shadow: 0 0 10px currentColor;
}
.logo:hover:before {
    background: conic-gradient(#e61a1a, #e6801a, #e6e61a, #80e61a, #1ae61a, #1ae680, #1ae6e6, #1a80e6, #1a1ae6, #801ae6, #e61ae6, #e61a80, #e61a1a);
    -webkit-animation: hoverWhirl 600ms linear infinite;
            animation: hoverWhirl 600ms linear infinite;
    -webkit-box-shadow: 0 0 1em #fff;
            box-shadow: 0 0 1em #fff;
    -webkit-transition: all 300ms cubic-bezier(0.42, 0, 0.58, 1);
    transition: all 300ms cubic-bezier(0.42, 0, 0.58, 1);
}
.logo:hover .whirl {
    -webkit-animation: whirl 5s linear infinite;
            animation: whirl 5s linear infinite;
    -webkit-box-shadow: 0 0 1em rgba(255, 221, 74, .8) inset;
            box-shadow: 0 0 1em rgba(255, 221, 74, .8) inset;
}
.logo .whirl {
    position: absolute;
    margin: -2em;
    left: 50%;
    top: 50%;
    width: 4em;
    height: 4em;
    -webkit-border-radius: 50%;
            border-radius: 50%;
    z-index: 2;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: url("/static/logo.png") 50%/cover padding-box #fff;
    border: solid 3px transparent;
    -webkit-box-shadow: 0 0 5px #006272 inset;
            box-shadow: 0 0 5px #006272 inset;
}
@-webkit-keyframes whirl {
to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
}
}
@keyframes whirl {
to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
}
}
@-webkit-keyframes hoverWhirl {
to {
    -webkit-transform: rotate(-1turn);
            transform: rotate(-1turn);
}
}
@keyframes hoverWhirl {
to {
    -webkit-transform: rotate(-1turn);
            transform: rotate(-1turn);
}
}



