
@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Handlee);
body {
  margin: 0;
}
body ul {
    list-style: none;
}
body {
  overflow: hidden;
}
nav {
  padding: 0 10px;
  height: 80px;
  font-family: "Handlee", cursive;
  font-weight: bold;
}
nav:before{
  content: '';
  display: table;
}
nav:after{
  content: '';
  display: table;
  clear: both;
}
nav .logo {
    vertical-align: bottom;
}
nav a {
    text-decoration: none;
}
nav span.name {
    color: #fff;
    font-size: 2em;
    margin-left: 10px;
}



